var getSmalled = 0;
var firstSmallCheck = 0;
$(document).ready(function(){
    if(!firstSmallCheck){
        switchSliderBHK();
        firstSmallCheck = 1;
    }
    $(window).resize(function() {
        switchSliderBHK();
    });
});

function switchSliderBHK() {
    if($(window).width() <= '769' && !getSmalled){
        $.ajax({
            url : '/changeSliderForMobile',
            method : 'GET',
            success : function(data){
                getSmalled = 1;
                $('#slider').html(data);
            }
        });
    }
    else if($(window).width() > '769' && getSmalled){
        $.ajax({
            url : '/changeSliderForPc',
            method : 'GET',
            success : function(data){
                getSmalled = 0;
                $('#slider').html(data);
            }
        });
    }
}